@import url(https://rsms.me/inter/inter.css);
.bouncer{
    display: flex;
    justify-content: space-around;
    align-items: flex-end;
    width: 50px;
    height: 100px;
  }
  
  .bouncer div{
    width: 10px;
    height: 10px;
    background-color: #999999;
    border-radius: 50%;
    -webkit-animation: bouncing 0.5s cubic-bezier(.19, .57, .3, .98) infinite alternate;
            animation: bouncing 0.5s cubic-bezier(.19, .57, .3, .98) infinite alternate;
  }
  
  .bouncer div:nth-child(2){
    -webkit-animation-delay: 0.1s;
            animation-delay: 0.1s;
    opacity: 80%;
  }
  
  .bouncer div:nth-child(3){
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
    opacity: 60%;
  }
  
  .bouncer div:nth-child(4){
    -webkit-animation-delay: 0.3s;
            animation-delay: 0.3s;
    opacity: 40%;
  }
  
  @-webkit-keyframes bouncing{
    from{ transform: translateY(0) }
    to{ transform: translateY(-50px) }
  }
  
  @keyframes bouncing{
    from{ transform: translateY(0) }
    to{ transform: translateY(-50px) }
  }
html {
  font-family: "Inter", sans-serif;
}
@supports (font-variation-settings: normal) {
  html {
    font-family: "Inter var", sans-serif;
  }
}

:root {
  --text-color: rgb(11, 10, 29);
}

* {
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0;
  font-family: "Inter", sans-serif;
  font-size: 16px;
  letter-spacing: -0, 011em;
  background-color: white;
  color: rgb(11, 10, 29);
  color: var(--text-color);
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
}

main > div {
  width: 100%;
  margin: 0 auto;
}

header {
  margin: 30px 0;
}

a {
  text-decoration: none;
}

a h2 {
  color: black;
}

/* Art Item */
.art-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.art-container article {
  width: 100%;
  max-width: 250px;
  margin: 15px;
  background: #fff;
}

.art-container article:hover {
  cursor: pointer;
}

.art-container article .artist:hover {
  color: lightseagreen;
}

.art-container article p,
.art-container article h3 {
  margin: 10px 0;
}

.art-container img {
  width: 100%;
}

.art-container .art-class-medium {
  color: #999999;
}

/* Search */
.search-box {
  display: flex;
  flex-direction: column;
  text-align: center;
  margin: 20px 0 20px 0;
}

.workCount {
  margin: 20px 0;
  color: #999999;
}

input[type="search"] {
  width: 90%;
  padding: 20px 20px;
}

input[type="search"]:focus {
  background-color: white;
}

input,
input::-webkit-input-placeholder {
  font-size: 16px;
}

/* Detail Art */
.details-art {
  max-width: 1000px;
  margin: 20px auto;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.details-art-title {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: top;
  margin: 20px 0;
  border-bottom: 1px solid #e6e6e6;
}

.artist-name,
.artist-role {
  margin-bottom: 10px;
}

.details-art-title .artist-role {
  color: #999999;
}

.details-art-artist {
  text-align: right;
}

.details-art-info {
  width: 100%;
}

.details-art img {
  max-height: 500px;
  margin: 20px 0;
}

.details-art dt {
  margin-top: 15px;
  margin-bottom: 5px;
  color: #999999;
}

@media only screen and (max-width: 767px) {
  .art-container article {
    text-align: center;
    max-width: 500px;
  }

  .details-art {
    padding: 10px;
  }

  .details-art img {
    width: 100%;
  }

  .details-art-title {
    flex-direction: column;
  }
  .details-art-artist {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 20px 0;
    text-align: left;
  }
}

